.headerContainer {
    background-color: whitesmoke;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    gap: 50px;
    height: 70px;
    font-size: 20px;
    align-items: center;
    margin-right: auto;
    padding-top: 13px;
    border-bottom-color: black;
    border-bottom-width: 2px;
  }
  
  .brandText {
    background-color: rgba(255, 255, 255, 0);
    width: 300px;
    font-size: 32px;
    font-family: "Merriweather", sans-serif;
    color: #333333;
    text-align: left;
    line-height: normal;
    margin: 70px;
  }
  
  .container {
    width: 860px;
    height: 600px;
    padding: 2px 2px 2px 2px;
    box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.18);
    border-radius: 4px;
    box-sizing: border-box;
    position: absolute;
    margin-top: 70px;
    margin-left: 500px;
  }
  
  .titleText {
    display: flex;
    justify-content: center;
    font-size: 26px;
    font-weight: 700;
    padding-bottom: 14px;
    padding-top: 45px;
  }
  
  .subtitleText {
    display: flex;
    justify-content: center;
    font-size: 19px;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .lowerText {
    display: flex;
    justify-content: center;
    padding-top: 35px;
    gap: 6px;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
    color: grey;
  }
  
  .textField1 {
    position: absolute;
    top: 200px;
    margin-left: 85px;
    height: 30px;
    width: 300px;
